export default class LoadMoreBtn {
    constructor({ selector, isHidden = false }) {
      // Obine referina ctre butonul specificat de selector.
      this.button = this.getButton(selector);
  
      // Ascunde butonul dac `isHidden` este setat la true.
      isHidden && this.hide();
    }
  
    getButton(selector) {
      // Returneaz elementul din DOM care corespunde selectorului dat.
      return document.querySelector(selector);
    }
  
    enable() {
      // Activeaz butonul i schimb textul acestuia la "Load More".
      this.button.disabled = false;
      this.button.textContent = 'Load More';
    }
  
    disable() {
      // Dezactiveaz butonul i schimb textul acestuia la "Loading...".
      this.button.disabled = true;
      this.button.textContent = 'Loading...';
    }
  
    hide() {
      // Adaug clasa 'hidden' la buton pentru a-l ascunde.
      this.button.classList.add('hidden');
    }
  
    show() {
      // Elimin clasa 'hidden' de pe buton pentru a-l afia.
      this.button.classList.remove('hidden');
    }
  }